.header {
	display: flex;
	justify-content: space-between;
	margin: 60px 75px 60px 100px; }
.header__logo {
	& a {
		display: flex;
		align-items: center;
		& img {
			width: 44px;
			height: 44px;
			margin-right: 12px; } } }
.title {
	line-height: 1;
	& h1 {
		@include f(24px, 600);
		& span {
			color: #56ecf6; } }
	& p {}
	@include f(13px, 500); }

.navigation {
	display: flex;
	align-items: center;
	line-height: 50px; }
.navigation__item {
	margin-right: 35px; }
.navigation__item--btn {
	background-color: #fff;
	color: $darkblue;
	padding: 13px 30px;
	border-radius: 16px;
	margin-right: 8px;
	&:last-child {
		border-radius: 21px; } }
